body {
    /* background-color: #FFCC00; */
    padding: 0px;
    margin: 0;
    font-family: sans-serif;
}

.imageTips {
    max-width: 400px;
    border: 1px solid grey;
    margin: 5px 0;
}
span label {
    margin-right:20px;
}
span input {
    margin-left: 20px;
}
div.hidden {
    display: none;
}
 
.page-container {
    position: absolute;
    top: 0;
    width: 100%;
}

.rangeContainer {
    margin: 10px 35px 10px 25px;
    font-size: 13px;
    margin-bottom: 30px;
    max-width: 800px;
}

.levelContainer .rc-slider-track {
    height: 7px;
}
.dateContainer .rc-slider-track {
    display:none;
}
.dateContainer .rc-slider-dot.active {
    border-color: #e9e9e9;
}

.dateContainer .rc-slider-dot-active {
    border-color: #e9e9e9;
}
.rangeContainer .rc-slider-mark {
    font-size: 14px;
}

.filterContainer {
    padding-top: 5px;
}

.finePrint {
    font-size: 8pt;
}

.suggestLandscape {
    margin: 8px 0 8px 0;
    display: none;
}
@media screen and (orientation: portrait) and (max-width: 550px) {
    .suggestLandscape {
        display: inline-block;
    }
}

h1,
h2,
h3,
h4
p,
ul,
li {
    font-family: sans-serif;
}

.content {
    background-color: #FFF;
    padding: 55px 20px 20px 20px;
}

.content h2 {
    padding: 0;
    margin: 0;
}

.content li {
    margin-bottom: 10px;
}

/* Nav bar follows */
nav {
    position: fixed;
    width: 100%;
    display: block;
    z-index: 3333;
}

/* Wide nav */
.navWide {
    display: none;
    background: #000000;
    width: 100%;
    padding: 4px;
}

.navWide ul {
    list-style-type: none;
    margin: 0;
    padding: 4px;
    overflow: hidden;
    background-color: #000000;
}

.navWide li {
    float: left;
}

.navWide a {
    padding: 15px;
    color: #FFF;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: bold;

    border-width: 1px;
    padding: 3px 10px;
    border-color: green;
    border: 1px solid;
    border-radius: 6px;
    margin: 8px;
}

/* Narrow */
.navNarrow {
    padding: 0px;
    float: left;
    background: #000000;
    width: 100%;
    display: block;
    position: absolute;
}

/* Button */
.navNarrow i {
    float: left;
    color: #ffffff;
    padding: 3px 6px;
    border-color: green;
    border: 1.5px solid;
    border-radius: 6px;
    margin: 5px;
}
.navNarrowMenu {
    padding: 15px;
    color: #FFF;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 14pt;

    border-width: 1px;
    padding: 3px;
    margin: 10px 2px;
    display: inline-block;
}

.narrowLinks {
    display: block;
    float: left;
    clear: left;
    width: 100%;
    background: #111;
    -webkit-transition: max-height 0.1s ease-in-out;
    -o-transition: max-height 0.1s ease-in-out;
    transition: max-height 0.1s ease-in-out;
    -webkit-transition-timing-function: ease-in-out;
    -webkit-transition-duration: 0.1s;
    -webkit-transition-property: max-height;
    -o-transition-timing-function: ease-in-out;
       transition-timing-function: ease-in-out;
    -o-transition-duration: 0.1s;
       transition-duration: 0.1s;
    -o-transition-property: max-height;
    transition-property: max-height;max-height: 0;
    overflow: hidden;
    height: auto;
}

.show {
    /* NOTE: Must be manually set */
    max-height: 220px;
    /* https://css-tricks.com/using-css-transitions-auto-dimensions/ */
}

.navNarrow ul {
    padding: 0px;
}

.narrowLinks a {
    display: block;
    padding: 6px;
    margin: 4px;
    border: 4px;
    color: #FFF;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: bold;
}

@media (min-width: 480px) {
    .navWide {
        display: block;
    }
    .navNarrow {
        display: none;
    }
}



